.projectsCont h2{
    margin: 0;
    text-align: center;
    font-size: clamp(1.8rem, 5vw - 0.5rem, 3.5rem);
    color: var(--light-grey);
}

.projectsCont p{
    font-family: 'DM sans';
    margin: 0;
    font-weight: 300;
    text-align: center;
    color: var(--pale-green);
}

.projectsCont figure{
    position: relative;
    border-radius: 1rem;
margin: 0;
gap: 2rem;
margin-top: 2rem;
width: auto;
}

.projectsCont img{
  display: block;
 
  border-radius: 0.7rem;


    width: 100%;
}

 figure::before {
    content: '';
    position: absolute;
    top: -10px;
    left: -10px;
    right: -10px;
    bottom: -10px;
    border-radius: 0.7rem; /* slightly larger than the image border-radius */
    border: 1px solid var(--light-grey);
    z-index: 0; /* to place it behind the image */
    box-sizing: border-box; /* ensures the border is included in the size */
    pointer-events: none; 

  }
.sectionOne{
    display: flex;
    justify-content: center;
align-items: center;
    flex-direction: column;
}
.projectsCont{
    row-gap: 3rem;
    display: grid;
    width: 60%;
    justify-content: center;
}

@media(max-width: 900px){

    .projectsCont{
       
        width: 80%;
        justify-content: center;
    }
}